import Vue from 'vue';
import VueRouter from 'vue-router';

import contentView from './components/content-view';
import withdrawal from './components/withdrawal';
import contentPurchase from './components/content-purchase';
import contentManagement from './components/content-management';
import manageContent from './components/manage-content';
import manageDirectory from './components/manage-directory';
import purchases from './components/purchases';
import purchaseHistory from './components/purchase-history';

Vue.use(VueRouter);

export default new VueRouter({
    routes: [
        {path: '/', component: contentPurchase},
        {path: '/withdrawal', component: withdrawal},
        {path: '/content/add', component: manageContent},
        {path: '/directory/add', component: manageDirectory},
        {path: '/content/:id', component: contentView},
        {path: '/history', component: purchaseHistory},
        {path: '/manage', component: contentManagement},
        {path: '/purchases', component: purchases},
    ]
});


