import Vue from 'vue';
import './../system/quill-editor';
import componentTemplate from '../../pages/components/manage-content.html';

export default Vue.component('manage-content', {
    template: componentTemplate,
    data() {
        return {
            content: null
        }
    },
    mounted() {
    },
    watch: {
        content(content) {
            window.content = content;
        }
    }
})


