import Vue from 'vue';
import Quill from 'quill';
import componentTemplate from '../../pages/system/quill-editor.html';

let toolbarOptions = [
    ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
    ['blockquote', 'code-block'],

    [{ 'header': 1 }, { 'header': 2 }],               // custom button values
    [{ 'list': 'ordered'}, { 'list': 'bullet' }],
    [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
    [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
    [{ 'direction': 'rtl' }],                         // text direction

    [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
    [{ 'header': [1, 2, 3, 4, 5, 6, false] }],

    [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
    [{ 'font': [] }],
    [{ 'align': [] }],
    ['image', 'video', 'code-block']

    // ['clean']                                         // remove formatting button
];

export default Vue.component('quill-editor', {
    template: componentTemplate,
    props: {
        value: {
            required: true
        },
        readOnly: {
            type: Boolean,
            default: false
        }
    },
    data: () => ({
        editor: {},
    }),
    mounted() {
        this.editor = new Quill('#editor', {
            placeholder: this.readOnly ? '' : 'Редактировать здесь',
            theme: 'snow',
            readOnly: this.readOnly,
            modules: this.readOnly ? {toolbar: null} : {toolbar: toolbarOptions}
        });
        //
        if (this.value) {
            this.editor.setContents(this.value)
        }

        this.editor.on('text-change', () => {
            this.$emit('input', this.editor.getContents());
        });
    },
    methods: {
    }
});


