import Vue from 'vue';
import componentTemplate from '../../pages/components/purchase-history.html';

export default Vue.component('purchase-history', {
    template: componentTemplate,
    mounted() {
        $(document).ready(function () {
            $('#table').DataTable({
                paging: false,
                info: false,
                searching: false
            });
            let options = {
                animationEnabled: true,
                theme: "light2", // "light1", "light2", "dark1", "dark2"
                title:{
                    text: "Количество скачиваний/день"
                },
                axisY: {
                    title: "Колисчество"
                },
                data: [{
                    type: "column",
                    showInLegend: true,
                    legendMarkerColor: "grey",
                    legendText: "День",
                    dataPoints: [
                        {y: 3,  label: "10.11.15"},
                        {y: 7,  label: "11.11.15"},
                        {y: 11, label: "12.11.15"},
                        {y: 4,  label: "13.11.15"},
                        {y: 6,  label: "14.11.15"},
                        {y: 8,  label: "15.11.15"},
                        {y: 2,  label: "16.11.15"},
                        {y: 1,  label: "17.11.15"}
                    ]
                }]
            };
            $("#chartContainer").CanvasJSChart(options);
        });
    }
})


